import * as Yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { CreateUserCommand } from "../../models/commands/CreateUser";

const CreateUserSchema: Yup.SchemaOf<CreateUserCommand> = Yup.object().shape({

    name: Yup.string().nullable()
        .required("Nome  obrigatrio"),

    warName: Yup.string().nullable()
        .required("Nome de guerra  obrigatrio"),

    userName: Yup.string().nullable()
        .matches(/^[A-Za-z]+(?:[.][A-Za-z]+)*$/,"Usurio do sistema deve corresponder [a-z .]")
        .required("Usurio do sistema  obrigatrio"),

    badge: Yup.mixed()
        .required("Graduao  obrigatrio"),

    phoneNumber: Yup.string().nullable().required("Celular  obrigatrio")
        .min(15, "Nmero de celular invlido")
        .max(16, "Nmero de celular invlido"),

    rg: Yup.string().nullable().required("RG  obrigatrio")
        .min(11, "RG: xx.xxx.xxx-x")
        .max(13, "RG no  vlido"),

    cpf: Yup.string().nullable().required("CPF  obrigatrio")
        .min(14, "CPF: xxx.xxx.xxx-xx")
        .max(15, "CPF no  vlido"),

    email: Yup.string().nullable()
        .email("Dgite um email vlido")
        .required("Email  obrigatrio"),

    occupation: Yup.string().nullable().required("Funo  obrigatrio"),
})

export default yupResolver<CreateUserCommand>(CreateUserSchema);